import './scss/index.scss';
import Parallax from '../node_modules/parallax-js/src/parallax.js'

// Parallaxe
let scene = document.getElementById('scene');
let parallaxInstance = new Parallax(scene, {
    relativeInput: true
});
parallaxInstance.friction(0.05, 0.25);
parallaxInstance.limit(40, 1000);